import {Injectable} from '@angular/core';
import {RxDatetimeStruct} from './adapt-rx-datetime.types';

export function ADAPT_RX_DATETIME_ADAPTER_FACTORY(): AdaptRxDatetimeStructAdapter {
  return new AdaptRxDatetimeStructAdapter();
}

@Injectable({providedIn: 'root', useFactory: ADAPT_RX_DATETIME_ADAPTER_FACTORY})
export abstract class AdaptRxDatetimeAdapter<T> {
  /**
   * Converts a user-model of type `T` to an `RxDatetimeStruct` for internal use.
   */
  abstract fromModel(value: T | null): RxDatetimeStruct | null;

  /**
   * Converts an internal `RxDatetimeStruct` to a user-model of type `T`.
   */
  abstract toModel(date: RxDatetimeStruct | null): T | null;
}

@Injectable()
export class AdaptRxDatetimeStructAdapter extends AdaptRxDatetimeAdapter<RxDatetimeStruct> {
  /**
   * Converts RxDatetimeStruct => RxDatetimeStruct
   */
  fromModel(date: RxDatetimeStruct | null): RxDatetimeStruct | null {
    return date;
  }

  /**
   * Converts RxDatetimeStruct => RxDatetimeStruct
   */
  toModel(date: RxDatetimeStruct | null): RxDatetimeStruct | null {
    return date;
  }
}
